import React,{ useEffect} from 'react'
import CardList from '../components/CardList.jsx'
import SearchBox from '../components/SearchBox.jsx'
import Scroll from '../components/Scroll.jsx'
import ErrorBoundary from '../components/ErrorBoundary.jsx'
import { connect } from 'react-redux'
import { setSearchField,requestRobots} from '../actions'
import Header from '../components/Header.jsx'

const mapStateToProps = states => { 
    console.log(states)
    return {
        searchField: states.robotsReducer.searchField,
        robots: states.robotsReducer.robots,
        isPending: states.robotsReducer.isPending,
        error: states.robotsReducer.error
    }
}

const mapDispatchToProps = dispatch => {    
    return {
        onSearchChange: (event) => dispatch(setSearchField(event.target.value)),
        onRequestRobots: () => dispatch(requestRobots())
    }
}   
 function App(props)  {
    console.log(props)
    const {searchField, onSearchChange ,robots ,isPending, onRequestRobots} = props
   
    useEffect(() => onRequestRobots(),[])
        const filteredRobots = robots.filter(robot => {
            return robot.name.toLowerCase().includes(searchField.toLowerCase());
          })
        if(isPending) {
            return <h1>Loading</h1>
        } else {
  return (
    <div className='tc'>
        <Header />
        <SearchBox  searchChange={onSearchChange} />
        <Scroll>
            <ErrorBoundary>
        <CardList robots={filteredRobots} />
        </ErrorBoundary>
        </Scroll>
    </div>)}
  
}

export default connect(mapStateToProps,mapDispatchToProps)(App);